:root{
  --square-side-length: 25px
}

.square {
    width: var(--square-side-length);
    height: var(--square-side-length);
    border: 1px solid black;
    display: inline-block;
    color: black;
    text-align: center;
    font-size: medium;
  }

.square-current {
  background-color: yellow;
}

.square-in-row{
  background-color: #8BCCF7;

}

.square-black {
  background-color:black;
}

.row {
  width: var(15 * --square-side-length)
}

.board {
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 25%;
}